a {
  text-decoration: none;
  color: inherit;
}

p,
h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

li {
  list-style-type: none;
}

button {
  border: none;
  cursor: pointer;
  background-color: transparent;
}
picture {
  display: block;
}
